                              <option>Mini</option>
                              <option>Midi</option>
                              <option>Classic</option>
                              <option>ThreeAxle</option>
                              <option>DoubleDecker</option>
                              <option>Van</option>
                              <option>Truck</option>
                              <option>unspecified</option>
                           </select>
                        </div>
                        <template v-if="!veicleCategory == ''">
