                        <div class="col-lg-12 mt-2">
                           <div class="d-flex">
                              <div class="form-check form-check-inline">
                                 <input class="form-check-input" type="radio" value="Liste netto runden" id="inlineCheckbox1" v-model="checked">
                                 <label class="form-check-label" for="inlineCheckbox1">Liste netto runden</label>
                              </div>
                              <div class="form-check form-check-inline">
                                 <input class="form-check-input" type="radio" value="Kunde brutto runden" id="inlineCheckbox2" v-model="checked">
