               <div class="card-body p-3">
                  <div class="row">
                     <div class="col-lg-12">
                        <div class="card">
                           <div class="card-item-list d-flex flex-row">
                              <div class="card-item__heading bg-teal-light mr-3 flex-basis-3 rounded p-2">
                                 <h4 class="h4 text-center mb-0">
