                                 </h4>
                              </div>
                              <div class="card-item__heading bg-gray mr-3 flex-basis-3 rounded p-2">
                                 <h4 class="h4 text-center mb-0">
                                    Rabatt
                                 </h4>
                              </div>
                              <div class="card-item__heading bg-teal-dark flex-basis-3 rounded p-2">
