                                 </h4>
                              </div>
                           </div>
                        </div>
                        <div class="card mb-3">
                           <div class="card-item-list d-flex flex-row">
                              <div class="card-item__heading bg-yellow-light flex-basis-3 p-2" v-for="(price, indx) in totalprices" :key="indx">
                                 <h4 class="h4 text-center mb-0">
                                    {{ price }}
                                 </h4>
                              </div>
                           </div>
                        </div>
                        <div class="card mt-4">
                           <div class="d-flex justify-content-between align-items-center" v-for="(endprice) in endprices" :key="endprice.id">
                              <h4 class="h4 text-center mb-3">
                                 {{endprice.name}}
                              </h4>
                              <div class="card-item__heading text-center flex-basis-3 rounded p-2" :class="{'bg-yellow-light': endprice.name == 'Bruttopreis'}">
