</template>
      

<script>
import GoogleMap from "../modules/adminPanel/components/global/GoogleMap.vue";
import SettingBtn from "./buttons/SettingBtn.vue";

export default {
	name: "calculator",
	components: {
		GoogleMap,
		SettingBtn
	},
	data() {
		return {
			value: 1,
			stopsValue: 1,
			veicleCategory: "",
			timepickervalue: "",
			datepickervalue: "",
			checked: "Liste netto runden",
			selected: "transfer",
			stops: [
				{
					id: null,
					destination: "",
					duration: ""
				}
			],
			feilds: ["Listenpreis", "Rabatt", "Kundenpreis"],
			pricestable: [
				{
					id: 1,
					row: "Zeitleistung",
					price: "5.00 ",
					discount: "2.00 ",
					customerPrice: "3.00 "
				},
				{
					id: 2,
					row: "Wegleistung",
					price: "5.00 ",
					discount: "2.00 ",
					customerPrice: "3.00 "
				},
				{
					id: 3,
					row: "Fahrleistung",
					price: "5.00 ",
					discount: "2.00 ",
					customerPrice: "3.00 ",
					totalprice: true
				},
				{
					id: 4,
					row: "Parken",
					price: "5.00 ",
					discount: null,
					customerPrice: "3.00 "
				},
				{
					id: 5,
					row: "Sonntagszuschlag",
					price: "5.00 ",
					discount: null,
					customerPrice: "3.00 "
				},
				{
					id: 6,
					row: "Feiertagszuschlag",
					price: "5.00 ",
					discount: null,
					customerPrice: "3.00 "
				},
				{
					id: 7,
					row: "Nachtzuschlag",
					price: "5.00 ",
					discount: null,
					customerPrice: "3.00 "
				},
				{
					id: 8,
					row: "Aufpreis",
					price: "5.00 ",
					discount: null,
					customerPrice: "3.00 ",
					totalprice: true
				}
			],
			totalprices: ["5.00 ", "5.00 ", "5.00 "],
			endprices: [
				{
					id: 1,
					name: "Umsatzsteuer  19%",
					price: "0.00 "
				},
				{
					id: 2,
					name: "Rundungsdifferenz",
					price: "0.00 "
				},
				{
					id: 3,
					name: "Bruttopreis",
					price: "0.00 "
				}
			]
		};
	},
	methods: {
		handlStop() {
			if (this.stopsValue > this.stops.length && this.stopsValue != 9) {
				this.stops.push({
					id: this.stopsValue,
					dist: "",
					duration: ""
				});
			}
			if (this.stopsValue < this.stops.length) {
				this.stops.splice(1, 1);
			}
		},
		deleteStop(stop) {
			if (this.stops.length !== 1) this.stops.splice(stop, 1);
			this.stopsValue = this.stops.length;
		}
	}
};
</script>

